.programs{
    margin:80px auto;
    width:90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.program img{
    width:100%;
    border-radius: 10px;
    display: block;
}
.program{
    flex-basis: 31%;
  position :relative;
}
.caption{
    border-radius: 10px;
    top:0;
    left:0;
    bottom:0;
    right:0;
    position: absolute;
    background: rgba(0,15,152,0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color:#fff;
    cursor: pointer;
    opacity: 0;
    padding-top: 70%;
    transition: 0.4s;
}
.program:hover .caption{
    opacity: 1;
    padding-top: 0%;
}
.caption img{
    width:60px;
    margin-bottom: 10px;
}

/* media query */
@media(max-width:650px){
    .programs{
        flex-direction: column;

    }
    .program{
        flex-basis: 100%;
        margin:20px;
    }
}